import Metadata from "sap/ui/base/Metadata";
import UI5Object from "sap/ui/base/Object";

export const isInstanceOf = <T extends UI5Object>(oObject: UI5Object | unknown, ui5ObjectClass: new (...args: any[]) => T): oObject is T => {
    return UI5Object.isA(oObject as object, (ui5ObjectClass as unknown as ({ getMetadata(): Metadata })).getMetadata().getName());
}